import styles from "./css/Footer.module.css";
import { useTranslation } from "react-i18next";
import { useAppSelector } from "../app/hooks";
import { linkScrollToPosition } from "../utils/functions/linkScrollToPosition";

const Footer = () => {
  const { t } = useTranslation();
  const { userDevice, deviceWidth } = useAppSelector((state) => state.userDevice);

  return (
    <footer className={styles.footer}>
      <div
        className={styles.nameContainer}
        onClick={() => {
          linkScrollToPosition(0, userDevice);
        }}
      >
        <div className={styles.separator}></div>
        <span className={styles.initiales}>AG</span>
      </div>
      <nav className={styles.linkContainer}>
        <li>
          <a
            className={styles.footerLink}
            href="#about"
            onClick={() => {
              if (userDevice === "mobile") {
                deviceWidth === 500 ? linkScrollToPosition(820, userDevice) : linkScrollToPosition(740, userDevice);
              } else linkScrollToPosition(720, userDevice, true);
            }}
          >
            {t("header.aboutMe")}
          </a>
        </li>
        <li>
          <a className={styles.footerLink} href="#skills">
            {t("header.skills")}
          </a>
        </li>
        <li>
          <a
            className={styles.footerLink}
            href="#works"
            onClick={() => {
              if (userDevice === "mobile") {
                deviceWidth === 500 ? linkScrollToPosition(3400, userDevice) : linkScrollToPosition(3450, userDevice);
              } else linkScrollToPosition(2720, userDevice, true);
            }}
          >
            {t("header.works")}
          </a>
        </li>
      </nav>
    </footer>
  );
};
export default Footer;
